import { stopSubmit } from "redux-form";
import { securityAPI, usersAPI } from "../../api/api";

let SET_USER_DATE = "sao-data/auth/SET_USER_DATE";
let GET_CAPTCHA_URL_SUCCESS = "sao-data/auth/GET_CAPTCHA_URL_SUCCESS";

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null,
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATE:
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                ...action.payload,
            };

        default:
            return state;
    }
};
export const setAuthUserData = (userId, email, login, isAuth) => {
    return {
        type: SET_USER_DATE,
        payload: {
            userId,
            email,
            login,
            isAuth,
        },
    };
};

export const getCaptchaUrlSuccess = (captchaUrl) => {
    return {
        type: GET_CAPTCHA_URL_SUCCESS,
        payload: {
            captchaUrl,
        },
    };
};

export const getAuthUserData = () => async (dispatch) => {
    let response = await usersAPI.auth();
    if (response.data.resultCode === 0) {
        let { id, email, login } = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));
    }
};

export const login =
    (email, password, rememberMe, captcha) => async (dispatch) => {
        let response = await usersAPI.login(
            email,
            password,
            rememberMe,
            captcha
        );
        if (response.data.resultCode === 0) {
            dispatch(getAuthUserData());
        } else {
            if (response.data.resultCode === 10) {
                dispatch(getCaptchaUrl());
            }
            let message =
                response.data.messages.length > 0
                    ? response.data.messages[0]
                    : "Some error";
            dispatch(stopSubmit("login", { _error: message }));
        }
    };

export const logout = () => async (dispatch) => {
    let response = await usersAPI.logout();
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
};
export const getCaptchaUrl = () => async (dispatch) => {
    let response = await securityAPI.getCaptchaUrl();
    const captchaUrl = response.data.url;
    dispatch(getCaptchaUrlSuccess(captchaUrl));
};

export default authReducer;
